.page-body {
  min-width: 414px;
  margin: 0;
  padding: 0;
  font-family: "Mulish", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  line-height: 21px;
  background-color: $background;
  background-image: url('../img/background.png');
  background-position: top center;
  color: $basic_dark;
  overflow-x: hidden;

  min-height: 100%;

  display: flex;
  flex-direction: column;

  @media (min-width: $tablet) {
    background: $background url('../img/background-desk.png') no-repeat left center;
  }
}
